import {Box} from '@mui/material';
import ReactMapGL from 'react-map-gl';


const AddLocation = () => {
  return (
    <Box
      sx={{
        height: 400,
        position: 'relative',
      }}
    >
    <ReactMapGL>
    mapboxAccessToken={process.env.REACT_APP_MAP_TOKEN}

    </ReactMapGL>
    </Box>

    );
  };

export default AddLocation;